












import {Vue, Component, Prop} from 'vue-property-decorator'

  // 定义一个接口
  interface User {
      name: string,
      password: string
  }

@Component({})
export default class Header extends Vue {
  @Prop({default: ''})
  title: string
  /*
   iframe+hash标识符,验证跨域通信
   父页面：interview/http/origin.html
  */
  checkHashChange(){
    let hash = window.location.hash;
    if (/^#\//.test(hash)) {
      hash = hash.substring(2)
    }
    this.$emit('change-title', hash)
  }
  // 获取localStorage数据
  getLocalUserInfo(){
      let testUser:User = JSON.parse(localStorage.getItem('userInfo'));
      this.title = testUser.name
  }
  // 监听storage事件
  listenLocalStorage(){
    window.addEventListener('storage', e => {
      this.getLocalUserInfo();
    })
  }

  mounted(){
    window.onhashchange = this.checkHashChange;
    this.listenLocalStorage()
  }
}



