








import {Vue, Component} from 'vue-property-decorator'
import { setTimeout } from 'timers'

@Component({})
export default class Socket extends Vue {
  // data
  websocket: any = null
  echo: string = 'ppp'
  response: string = ''
  isOpen: boolean = false
  // methods
  // 实际调用的方法
  threadPoxl():void{
    // 参数
    const agentData: string = "mymessage"

    // 若ws开启状态
    if (this.websocket.readyState === this.websocket.OPEN) {
      console.log('threadPoxl open')
      this.websocketSend();
    }
    // 若ws正在开启状态，则等待200ms
    else if (this.websocket.readyState === this.websocket.CONNECTING) {
      console.log('threadPoxl opening')

      setTimeout(() => {
        this.websocketSend()
      }, 200);
    }
    // 若未开启， 则等待500ms
    else {
      console.log('threadPoxl closed')
      this.initWebSocket()
      setTimeout(() => {
        this.websocketSend()
      }, 500);
    }
  }
  // 初始化websocket
  initWebSocket():void{
    // ws 地址
    const ECHO_URI = 'ws://echo.websocket.org'
    const LOCAL_URI = 'ws://localhost:3000/nodews/fui/socket'

    // this.websocket = new WebSocket(ECHO_URI)
    this.websocket = new WebSocket(LOCAL_URI)
    this.websocket.onopen = this.websocketOnopen;
    this.websocket.onmessage = this.websocketOnmessage
    this.websocket.onclose = this.websocketClose
    // this.threadPoxl()
  }
  websocketOnopen(){
      console.log('ws open')
      this.isOpen = true
  }
  // 数据接收
  websocketOnmessage(e):void{
    this.response = e.data
    console.log('receive message from ws server:', e.data)
  }
  // 数据发送
  websocketSend():void{
    this.websocket.send(this.echo)
  }
  // 关闭
  websocketClose(e):void{
    this.isOpen = false
    console.log('socket closed:', e.code)
  }

  // created
  created(){
    // this.initWebSocket();
  }
}



