















import { Vue, Component } from "vue-property-decorator";

@Component({})
export default class TBFloatLayout extends Vue {
    layout: string = "tb-layout"
    dataList: number[] = []
    principleImgUrls: string[] = []

    // methods
    back(): void{
          this.$router.go(-1);
    }
    created(){
      for (let i = 0 ; i < 10; i++){
        this.dataList.push(i);
      }
    }
}




