









import { Vue, Component, Provide, Inject } from 'vue-property-decorator'
import ipstService from './service.js'
@Component({})
export default class DataType extends Vue {
  // data
  response: object = {
    result: {},
    content:{}
  }

  // methods
  sendFetch () {
    ipstService.getCars().then(res => {
      this.response = res
    }).catch(err => {
      alert('请求超时')
    })
  }
}



